/*HOW TO*/
/*CATALOG*/
.catalog_full {
  background-image: url(../img/layout/pattern007.png); }

.catalog_blocks {
  width: 1203px;
  display: table;
  margin: auto;
  overflow: auto;
  background-color: #fff;
