.one_item {
  width: 27%;
  background-color: #d9e7f1;
  margin-bottom: 5%;
  padding: 10px 0px 25px 0px;
  margin-left: 6%;
  float: left; }
  .one_item .img {
    background-color: #d9e7f1;
    max-width: 300px;
    max-height: 225px;
    min-width: 300px;
    min-height: 225px;
    display: table;
    margin: auto;
    position: relative;
    padding-left: 10px;
    padding-right: 10px; }
    .one_item .img:before, .one_item .img:after {
      content: " ";
      display: table; }
    .one_item .img:after {
      clear: both; }
    .one_item .img .add_to {
      margin-top: 3px;
      font-size: 16px;
      color: #073559; }
      .one_item .img .add_to p {
        display: inline-block;
        vertical-align: top;
        margin-top: 16px;
        margin-bottom: 0; }
    .one_item .img .fa-heart-o {
      color: #073559;
      /* position: absolute; */
      /* top: 0px; */
      /* right: 20px; */
      margin-top: 7px;
      vertical-align: top;
      margin-right: 20px;
      float: left; }
  .one_item .item_img {
    max-width: 300px;
    max-height: 225px;
    display: table;
    margin: auto; }
  .one_item .item_title {
    font-weight: 600;
    overflow: hidden;
    color: #073559;
    font-size: 36px;
    margin-left: 15px; }
    .one_item .item_title a {
      color: inherit;
      cursor: pointer; }
      .one_item .item_title a:hover {
        text-decoration: none; }
  .one_item .short_title .second {
    font-size: 24px;
    min-height: 30px;
    max-height: 30px; }
  .one_item .short_descr {
    position: relative; }
    .one_item .short_descr .item_descr {
      overflow: hidden;
      max-height: 100px;
      min-height: 100px; }
      .one_item .short_descr .item_descr:before {
        content: "";
        display: block;
        position: relative;
        z-index: 2;
        height: 20px;
        top: 80px;
        background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #d9e7f1 75%); }
      .one_item .short_descr .item_descr:hover + .full {
        display: block;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 10;
        background-color: #d9e7f1;
        height: auto;
        width: auto;
        max-height: initial; }
        .one_item .short_descr .item_descr:hover + .full:before {
          visibility: hidden; }
    .one_item .short_descr .full:hover {
      display: block;
      position: absolute;
      top: 0px;
      left: 0px;
      z-index: 10;
      background-color: #d9e7f1;
      height: auto;
      width: auto;
      max-height: initial; }
      .one_item .short_descr .full:hover:before {
        visibility: hidden; }
  .one_item .item_descr {
    font-size: 14px;
    color: #212121;
    padding-bottom: 10px; }
    .one_item .item_descr table {
      margin-left: 15px; }
      .one_item .item_descr table td {
        padding-right: 20px; }
  .one_item .price {
